form {
  text-align: center;
  display: grid;
}

label {
  display: block;
  padding-bottom: 1em;
}

input {
  background: #FFC43D;
  border: none;
  border-radius: 25px;
  font-size: 18px;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  filter: invert(71%) sepia(48%) saturate(6538%) hue-rotate(148deg) brightness(85%) contrast(79%);
}

button {
  background: #1B9AAA;
  color: #F8FFE5;
  border: none;
  border-radius: 25px;
  font-size: 18px;
  margin-bottom: 1em;
}


.mpg {
  font-size: 5em;
}
